import {
  OnChanges,
  Component,
  OnInit,
  Input,
  ViewChild,
  ElementRef
} from "@angular/core";
import  TreeChart from "d3-org-chart";
import { MatSliderChange } from '@angular/material/slider';

@Component({
  selector: "app-d3-org-chart",
  templateUrl: "./d3-org-chart.component.html",
  styleUrls: ["./d3-org-chart.component.scss"]
})
export class D3OrgChartComponent implements OnInit, OnChanges {
  @ViewChild("chartContainer") chartContainer: ElementRef;
  @Input() data: any[];
  chart;
  scale :number =0.4;
  initialTransalte :string;

  constructor() {}

  ngOnInit() {}

  ngAfterViewInit() {
    if (!this.chart) {
      this.chart = new TreeChart();
    }
    this.updateChart();
    console.log('ngAfterViewInit')
  }

  ngOnChanges() {
    this.updateChart();
    console.log('ngOnChanges')
  }
  updateChart(reset:boolean=false,updateData:any[]=[]) {
    if(!reset){
      if (!this.data) {
        return; 
      }
      if (!this.chart) {
        return; 
      }
    }else{
      this.data=updateData;
    }
    console.log('Helo'+this.data);
    this.chart
      .container(this.chartContainer.nativeElement)
      .data(this.data)
      .initialZoom(this.scale)
      .onNodeClick(d => console.log(d + " node clicked"))
      .render();
     
      
  }
  onInputChange(event: MatSliderChange) {
    this.scale=event.value;
    console.log(this.scale);
    let svg = this.chartContainer.nativeElement.querySelectorAll('g')[1];
    svg.setAttribute("transform", svg.getAttribute("transform").match(/translate(.*)scale/)[0]+"("+String(this.scale)+")");

  }
}
