import { Component, OnInit, Output, EventEmitter, Input, OnChanges, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { FamilyData } from '../layout/header/header.component';
import { D3OrgChartComponent } from '../d3-org-chart/d3-org-chart.component';

@Component({
    selector: 'app-family-tree',
    templateUrl: './family-tree.component.html',
    styleUrls: ['./family-tree.component.scss']
})
export class FamilyTreeComponent implements OnInit ,OnChanges{
    @Output() familyData: EventEmitter<any> = new EventEmitter<any>();
    @Input() selectedMember : FamilyData;
    @ViewChild(D3OrgChartComponent) d3OrgChartComponent:D3OrgChartComponent;
    data: any = [];
    templateHTML1 = "<div>\n                  <div style=\"margin-left:20%;\n                              margin-top:10%;\n                              font-size:200%;\n                              font-weight:bold;\n                         \">"
    templateHTML2 = "</div>\n                 <div style=\"margin-left:20%;\n                              margin-top:10%;\n                              font-size:170%;\n                         \">"
    tempData = {
        "width": 342,
        "height": 146,
        "borderRadius": 5,
        "borderColor": {
            "red": 15,
            "green": 140,
            "blue": 121,
            "alpha": 1
        },
        "nodeImage": {
            "url": "https://www.pinclipart.com/picdir/middle/200-2008697_account-customer-login-man-user-icon-login-icon.png",
            "width": 100,
            "height": 100,
            "centerTopDistance": 0,
            "centerLeftDistance": 0,
            "cornerShape": "CIRCLE",
            "shadow": false,
            "borderWidth": 0,
            "borderColor": {
                "red": 19,
                "green": 123,
                "blue": 128,
                "alpha": 1
            }
        }

    }
    constructor(private http: HttpClient) {
    }
    public deepCopy(obj) {
        var copy;

        // Handle the 3 simple types, and null or undefined
        if (null == obj || "object" != typeof obj) return obj;

        // Handle Date
        if (obj instanceof Date) {
            copy = new Date();
            copy.setTime(obj.getTime());
            return copy;
        }

        // Handle Array
        if (obj instanceof Array) {
            copy = [];
            for (var i = 0, len = obj.length; i < len; i++) {
                copy[i] = this.deepCopy(obj[i]);
            }
            return copy;
        }

        // Handle Object
        if (obj instanceof Object) {
            copy = {};
            for (var attr in obj) {
                if (obj.hasOwnProperty(attr)) copy[attr] = this.deepCopy(obj[attr]);
            }
            return copy;
        }

        throw new Error("Unable to copy obj! Its type isn't supported.");
    }

    public getJSON(): Observable<any> {
        return this.http.get("./assets/data.json");
    }
    ngOnChanges(){
        
        for (var i = 0; i < this.data.length; i++) {
            if(this.selectedMember['nodeId']==this.data[i]['nodeId']){
                this.data[i]['expanded']=true;
                console.log(this.selectedMember['nodeId'])
            }
            else{
                this.data[i]['expanded']=false;
            }
        }
        this.familyData.emit(this.data);
        this.d3OrgChartComponent.updateChart(true,this.data);
    }

    ngOnInit() {
        this.getJSON().subscribe(data => {
            var dataArray: any = []
            for (var i = 0; i < data.length; i++) {
                var temp = this.deepCopy(this.tempData);
                temp['nodeId'] = data[i]['nodeId']
                temp['name'] = data[i]['name']
               
                temp['template'] = this.templateHTML1 + data[i]['name'] + "</div>" + this.templateHTML2 + data[i]['nodeId'] + "</div>"
                temp['parentNodeId'] = data[i]['parentNodeId']
                if ('expanded' in data[i]) {
                    temp['expanded'] = data[i]['expanded']
                }

                if ('profilePic' in data[i]) {
                    temp['nodeImage']['url'] = data[i]['profilePic']
                    temp['flag'] = data[i]['profilePic']
                }else{
                    temp['flag'] =  temp['nodeImage']['url']
                }
                dataArray.push(temp);
            }
            this.data = dataArray;
            this.familyData.emit(dataArray);
        });
    }

}
