import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import { LayoutRoutingModule } from './layout-routing.module';
import { LayoutComponent } from './layout.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { HeaderComponent } from './header/header.component';
import { FamilyTreeModule } from '../family-tree/family-tree.module';
import { FamilyTreeComponent } from '../family-tree/family-tree.component';
import { D3OrgChartComponent } from '../d3-org-chart/d3-org-chart.component';
import { DemoMaterialModule } from './header/material-module';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
@NgModule({
    imports: [
        CommonModule,FormsModule,
        LayoutRoutingModule,
        ReactiveFormsModule,
        DemoMaterialModule
    ],
    
  providers: [
    { provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: { appearance: 'fill' } },
  ],
    declarations: [LayoutComponent, SidebarComponent, HeaderComponent,D3OrgChartComponent,FamilyTreeComponent]
})
export class LayoutModule {}