import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

export interface FamilyData {
    flag: string;
    name: string;
    population: string;

}

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})

export class HeaderComponent implements OnInit, OnChanges {

    public pushRightClass: string;
    currentUserName: string;
    @Input() familyData: any = [];
    @Output() searchedMember: EventEmitter<any> = new EventEmitter<any>();
    currentMember: FamilyData = null;
    constructor(public router: Router,private matIconRegistry: MatIconRegistry,
        private domSanitizer: DomSanitizer){
        this.matIconRegistry.addSvgIcon(
          `logo`,
          this.domSanitizer.bypassSecurityTrustResourceUrl(`assets/svg/logo.svg`)
        );

        this.currentUserName = 'Admin'
        this.filteredStates = this.stateCtrl.valueChanges
            .pipe(
                startWith(''),
                map(state => state ? this._filterStates(state) : this.states.slice())
            );
    }
    stateCtrl = new FormControl();
    filteredStates: Observable<FamilyData[]>;

    states: FamilyData[] = [];


    ngOnChanges() {
        this.states = this.familyData;
        console.log(this.familyData)
    }

    private _filterStates(value: string): FamilyData[] {
        const filterValue = value.toLowerCase();

        return this.states.filter(state => state.name.toLowerCase().indexOf(filterValue) === 0);
    }

    ngOnInit() {
        this.pushRightClass = 'push-right';


    }

    isToggled(): boolean {
        const dom: Element = document.querySelector('body');
        return dom.classList.contains(this.pushRightClass);
    }

    toggleSidebar() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle(this.pushRightClass);
    }

    rltAndLtr() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle('rtl');
    }

    onLoggedout() {
        localStorage.removeItem('isLoggedin');
    }
    selectedMember(member:FamilyData) {
        this.searchedMember.emit(member)
    }
    // changeLang(language: string) {
    //     this.translate.use(language);
    // }

}
